<template>
    <div class="prize-info">
        <div class="order-content">
            <div class="content-top">
                <p>{{ $L('订单号') }}：{{ prizeInfo.sn }}</p>
                <div class="step-bar">
                    <div class="steps" >
                        <p class="status mainBgColor">{{ $L('申请领奖') }}<i class="icon el-icon-arrow-right"></i></p>
                        <p class="time">{{ prizeInfo.createTime }}</p>
                    </div>
                    <div class="steps">
                        <p class="status" :class="{mainBgColor:prizeInfo.deliveryStatus >= 1}">{{ $L('卖家发货') }}<i class="icon el-icon-arrow-right" v-if="prizeInfo.deliveryStatus >= 1"></i></p>
                        <p class="time" v-if="prizeInfo.deliveryStatus >= 1 && prizeInfo.confirmTime">{{ prizeInfo.confirmTime }}</p>
                        <p class="time" v-else>{{ prizeInfo.deliveryIngTime }}</p>
                    </div>
                    <div class="steps">
                        <p class="status" :class="{mainBgColor:prizeInfo.deliveryStatus >= 3}">{{ $L('确认收货') }}<i class="icon el-icon-arrow-right"  v-if="prizeInfo.deliveryStatus >= 3"></i></p>
                        <p class="time">{{ prizeInfo.confirmDeliveryTime }}</p>
                    </div>
                </div>
            </div>
            <div class="order-details" v-if="ptype !== 'Virtual'">
                <div class="order-left">
                    <p class="title">{{ $L('订单信息') }}</p>
                    <order-detail v-bind:orderInfo="prizeInfo"></order-detail>
                </div>
                <div class="order-right">
                    <div class="right-content">
                        <p class="title">{{ $L('物流信息') }}</p>
                        <div class="deliver" v-if="deliveryList.length==0">
                            {{ $L('暂时未有物流信息') }}
                        </div>
                        <div v-if="deliveryList.length>0">
                            <div class="order-number">
                                {{ orderInfo.deliveryName }}：{{ orderInfo.deliveryNum }}
                            </div>
                            <logistics-info v-bind:deliveryList="deliveryList"></logistics-info>
                        </div>

                    </div>

                </div>
            </div>
            <div class="prod-info">
                <prod-list v-bind:orderInfo="prizeInfo"></prod-list>
            </div>
            <div class="bottom-btns">
                <el-button type="primary" @click="backFunc">{{ $L('返回') }}</el-button>
            </div>
        </div>
    </div>

</template>

<script>
    import OrderDetail from "../../../components/prize/orderDetail";
    import LogisticsInfo from "../../../components/prize/LogisticsInfo";
    import ProdList from "../../../components/prize/prodList";
    export default {
        name: "prizeInfo",
        components: {ProdList, LogisticsInfo, OrderDetail},
        data () {
            return {
                loading: false,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                prizeInfo: {},
                deliveryList:[],
                ptype:''
            }
        },
        created () {
           this.getData();
           if (this.$route.params.ptype !== '') {
               this.ptype = this.$route.params.ptype;
           }
        },
        methods : {
            backFunc(){
                this.$router.push({name:'prizeList',params:{type:'prod'}});
            },
            getData (){
                if(this.$route.params.id !==''){
                    const _self = this;
                    _self.loading = true;
                    let id = _self.$route.params.id;
                    _self.$api('getPrizeDetail', {id:id} , function (reData) {
                        if (reData.flag === 1) {
                            _self.prizeInfo = reData.data;
                            _self.deliveryList = reData.data.deliveryList;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name: 'prizeList',params: {type: 'prod'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'prizeList',params: {type: 'prod'}});
                        });
                    });
                }

            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    .prize-info{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        .order-content{
            position: absolute;
            width: calc(100% - 40px);
            font-size: 14px;
            margin-top: 10px;
            margin-bottom: 20px;
            .content-top{
                padding: 20px;
                background: #fff;
                .step-bar{
                    margin-top: 15px;
                    width: 65%;
                    .steps{
                        display: inline-block;
                        width: calc(100%/3);
                        vertical-align: top;
                        .status{
                            height: 30px;
                            line-height: 30px;
                            position: relative;
                            padding: 0 20px;
                            background: #eee;
                            .icon{
                                height: 30px;
                                line-height: 30px;
                                position: absolute;
                                right: 0;
                                font-size: 30px;
                            }
                            &.mainBgColor{
                                color: #fff;
                            }
                        }
                        .time{
                            padding-top: 15px;
                        }

                    }
                }
            }
            .order-details{
                margin-top: 20px;
                .title{
                    font-weight: 700;
                    margin-bottom: 10px;
                }
                .order-left{
                    width: calc((100% - 20px)/2);
                    background: #fff;
                    display: inline-block;
                    padding: 20px;
                    height: 360px;
                    vertical-align: top;
                }
                .order-right{
                    width: calc((100% - 20px)/2);
                    background: #fff;
                    display: inline-block;
                    margin-left: 20px;
                    padding: 20px;
                    height: 360px;
                    vertical-align: top;
                    .right-content {
                        overflow-x: hidden;
                        .logistics-info{
                            height: 230px;
                            overflow-y: scroll;
                        }
                    }
                    .order-number{
                        margin-bottom: 10px;
                        line-height: 42px;
                    }

                }
            }
            .bottom-btns{
                padding: 40px 20px;
                background: #fff;
                text-align: right;
                button{
                    width: 100px;
                }
            }
        }

    }
    .error {
        position: absolute;
        z-index: 200;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #FFFFFF;
        overflow: hidden;

        .error-tips {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 320px;
            transform: translate(-50%, -50%);
        }
    }
</style>